<template>
  <div>
    <el-card class="contentCard">
      <!-- 标题 -->
      <el-row :gutter="24" type="flex" justify="center">
        <el-col :span="16">
          <div class="title">{{ article.title }}</div>
        </el-col>
      </el-row>
      <!-- 文章属性 -->
      <el-row :gutter="24" type="flex" justify="space-around" align="top">
        <el-col :span="4"
          ><div>笔记分类：{{ article.cate }}</div></el-col
        >
        <el-col :span="4"
          ><div>时间：{{ article.pub_date }}</div></el-col
        >
      </el-row>
      <!-- 文章正文 -->
      <el-row :gutter="24" type="flex" justify="center">
        <el-col :span="16" class="inputDeep">
          <!-- <div v-html="article.content" class="content"></div> -->
          <el-input
            type="textarea"
            clearable
            :rows="30"
            v-model="article.content"
            :readonly="true"
          >
          </el-input>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 指定文章id
      id: 0,
      // 接收文章信息的对象
      article: {}
    }
  },
  created() {
    this.getArticle()
  },
  methods: {
    // 通过id获取指定文章
    async getArticle() {
      this.id = window.sessionStorage.getItem('noteId')
      // console.log(this.id)
      const { data: res } = await this.$http.post(
        '/api/getArticleById?id=' + this.id
      )
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('数据异常')
      }
      this.article = res.data[0]
      // this.article.content = this.article.content.replace(/\s/g, '<br/>')
    }
  }
}
</script>

<style lang="less" scoped>
.contentCard {
  font-size: 16px;
  height: 735px;
}
.title {
  font-size: 24px;
  text-align: center;
  border-bottom: 1px solid #000;
  padding-bottom: 10px;
}
// .content {
//   font-size: 16px;
//   letter-spacing: 1px;
//   line-height: 10px;
//   border-top: 1px dashed #000;
//   margin-top: 1px;
//   padding: 10px 0;
// }
/*  el-input type='textarea' ，CSS穿透去除边框 父级>>>input */
.inputDeep /deep/ .el-textarea__inner {
  border: 0;
  border-top: 1px dashed #000;
  margin-top: 2px;
  /*这个是去掉 textarea 下面拉伸的那个标志*/
  resize: none;
}
</style>
